import styles from './SignUpPage.module.scss'
import AuthInput from "../../components/AuthInput/AuthInput.jsx";
import Button from "../../components/Button/Button.jsx";
import Swal from 'sweetalert2';
import AuthPageContainer from "../../components/AuthPageContainer/AuthPageContainer.jsx";
import { useMemo, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { userSignUp } from '../../api/auth';



export default function SignUpPage() {
  const [account, setAccount] = useState('')
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [checkPassword, setCheckPassword] = useState('')
  const [msg, setMsg] = useState('')
  const navigate = useNavigate()

  //
  const isValid = useMemo(() => {
    if (!account || account.length > 50) {
      return false
    }
    if (!name || name.length > 50) {
      return false
    }
    if (!email || !email.includes('@')) {
      return false
    }
    if (!password) {
      return false
    }
    if (!checkPassword) {
      return false
    }

    return true
  }, [account, name, email, password, checkPassword])

  const handleClick = async () => {
    if (!isValid) {
      Swal.fire({
        toast: true,
        position: "top",
        title: "!",
        icon: "error",
        timer: 1000,
        showConfirmButton: false,
      });
      // setMsg("!");
      return
    }

    //data
    const data = await userSignUp({
      account, name, email, password, checkPassword
    })

    //signup noti
    if (data.status === "success") {
      Swal.fire({
        toast: true,
        position: "top",
        title: "!",
        icon: "success",
        timer: 1000,
        showConfirmButton: false,
      });      
      //
      navigate('/login')
      return
    }

    //failed message from server
    Swal.fire({
      toast: true,
      position: "top",
      title: data.response.data.message,
      icon: "error",
      timer: 1000,
      showConfirmButton: false,
    });
    setMsg(data.response.data.message)
  }

  return (
    <AuthPageContainer title=''>
      <AuthInput
        labelName=''
        type='text'
        value={account}
        placeholder=''
        onChange={(accountInput) => setAccount(accountInput)}
        notification={msg === "account " ? msg : ""}
        lengthLimit={50}
      />
      <AuthInput
        labelName=''
        type='text'
        value={name}
        placeholder=''
        onChange={(nameInput) => setName(nameInput)}
        notification=''
        lengthLimit={50}
      />
      <AuthInput
        labelName='Email'
        type='text'
        value={email}
        placeholder=' Email'
        onChange={(emailInput) => setEmail(emailInput)}
        notification={msg === "email " ? msg : ""}
        lengthLimit={50}
      />
      <AuthInput
        labelName=''
        type='password'
        value={password}
        placeholder=''
        onChange={(passwordInput) => setPassword(passwordInput)}
        notification=''
        lengthLimit={50}
      />
      <AuthInput
        labelName=''
        type='password'
        value={checkPassword}
        placeholder=''
        onChange={(checkPasswordInput) => setCheckPassword(checkPasswordInput)}
        notification={msg === "!" ? msg : ""}
        lengthLimit={50}
      />
      <Button size='extraLarge' title='' onClick={handleClick} />

      {/* switch link */}
      <div className={styles.linkContainer}>
        <Link to='/login'>
          <span className={styles.link}></span>
        </Link>
      </div>
    </AuthPageContainer>
  );
}