import styles from './LoginPage.module.scss'
import { useState, useEffect } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import AuthPageContainer from '../../components/AuthPageContainer/AuthPageContainer.jsx'
import AuthInput from '../../components/AuthInput/AuthInput.jsx'
import Button from '../../components/Button/Button.jsx'
import { useAuthContext } from '../../contexts/AuthContext.jsx'
import Swal from 'sweetalert2'

export default function LoginPage() {
  const [account, setAccount] = useState('')
  const [password, setPassword] = useState('')
  const navigate = useNavigate()
  const { login, isAuthenticated } = useAuthContext()
  const [msg, setMsg] = useState('')

  //handleClick
  const handleClick = async () => {
    //CCheck if the account and password have been entered.
    if (!account || !password) {
      Swal.fire({
        toast: true,
        position: "top",
        title: "",
        icon: "warning",
        timer: 1000,
        showConfirmButton: false,
      });

      setMsg("");
      return;
    }

    //Account and password have been entered
    const success = await login({ account, password });
    if (success) {
      //response: success
      Swal.fire({
        toast: true,
        position: "top",
        title: "",
        icon: "success",
        timer: 1000,
        showConfirmButton: false,
      });
      return;
    }
    //login failed
    Swal.fire({
      toast: true,
      position: "top",
      title: "!",
      icon: "error",
      timer: 1000,
      showConfirmButton: false,
    });
    setMsg("!");
  }

  // redirection
  useEffect(() => {
    //
    if(isAuthenticated) {
      navigate('/main')
    }
  }, [isAuthenticated, navigate])

  return (
    <AuthPageContainer title=' Alphitter'>
      <AuthInput
        labelName=''
        type='text'
        value={account}
        placeholder=''
        onChange={(accountInput) => setAccount(accountInput)}
        notification={
          msg === "!" || msg === "" ? msg : 0
        }
        lengthLimit={50}
      />
      <AuthInput
        labelName=''
        type='password'
        value={password}
        placeholder=''
        onChange={(passwordInput) => setPassword(passwordInput)}
        notification={
          msg === "" ? msg : 0
        }
        lengthLimit={50}
      />
      <Button size='extraLarge' title='' onClick={handleClick} />

      {/* switch link */}
      <div className={styles.linkContainer}>
        <Link to='/signup'>
          <span className={styles.link}></span>
        </Link>
        <span></span>
        <Link to='/admin/login'>
          <span className={styles.link}></span>
        </Link>
      </div>
    </AuthPageContainer>
  );
}

